'use client';

import { useEffect } from 'react';

export default function CampaignModalSimple({ isOpen, onClose, childData }) {
  // Close modal on Escape key press
  useEffect(() => {
    const handleEscape = (e) => {
      if (e.key === 'Escape') onClose();
    };

    if (isOpen) {
      document.addEventListener('keydown', handleEscape);
      // Prevent body scroll when modal is open
      document.body.style.overflow = 'hidden';
    }

    return () => {
      document.removeEventListener('keydown', handleEscape);
      document.body.style.overflow = 'unset';
    };
  }, [isOpen, onClose]);

  if (!isOpen) return null;

  // Default data
  const data = childData || {
    name: 'জুনায়েদ হোসেন সকাল',
    subtitle: 'ঢাকা ও আশার',
    date: '২১ জানুয়ারি, ২০১৮',
    description: [
      'বাংলা পাবলিক লাইব্রেরি সকল শিশু সুবিধা প্রদান করে। পদ্ধতিটি ২১ জানুয়ারী ২০১৮ সালে শুরু হয়েছিল। আমরা পরিবারে ১ নম্বর, বইয়ে উপরে আগ্রহী করা সম্ভবতা।',
      'যখনই সুবিধা শিশুরা সব ব্যাক্তিসিত্বে জড়ু করে বাহিরে সময় এবং পছন্দ শিশু প্রতিবার জন্য নীতিবন্ধ সাহায্য দিন নিজে হাসপার নীতিনীতি জন্য শক্তি শক্তি করে পাবলিক লাইব্রেরি।',
      'সুপ্রাচীন অ্যাসাইনার শুধুই আমের কিন্তু আমরা এবং সম্পাদন আশ্রম এবং পদ্ধতি শক্তি শিক্ষক, পরিষেবা পাবলিক লাইব্রেরি শুরু শক্তি সাহায্য বুদ্ধিশক্তি, বাংলা অন্যান্য।',
      'সামনে অ্যাসাইনার শুরু দিন স্বাভাবিক শিক্ষক ছিল করে শক্তি আমার দিয়ে। এবং লিখতে পরিবার শক্তি শিখিয়ে পরিবার কখন নাম নাম পাবলিক পুস্তকের পদ্ধতি বন্ধু।'
    ],
    childPhoto: '/child-photo.jpg',
    galleryPhotos: ['/gallery-1.jpg', '/gallery-2.jpg', '/gallery-3.jpg', '/gallery-4.jpg'],
    videoId: 'YOUR_VIDEO_ID'
  };

  return (
    <div
      className="fixed inset-0 z-50 flex items-center justify-center p-4 bg-black bg-opacity-60 backdrop-blur-sm overflow-y-auto"
      onClick={onClose}
    >
      {/* Modal Content */}
      <div
        className="relative w-full max-w-2xl bg-gradient-to-b from-gray-800 to-gray-900 rounded-2xl shadow-2xl my-8"
        onClick={(e) => e.stopPropagation()}
      >
        {/* Floating Close Button */}
        <button
          onClick={onClose}
          className="absolute -top-3 -right-3 z-10 bg-red-600 hover:bg-red-700 text-white rounded-full w-10 h-10 flex items-center justify-center shadow-lg transition-all duration-200 hover:scale-110 focus:outline-none focus:ring-2 focus:ring-red-400"
          aria-label="Close modal"
        >
          <svg
            className="w-6 h-6"
            fill="none"
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth="2.5"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path d="M6 18L18 6M6 6l12 12"></path>
          </svg>
        </button>

        {/* Modal Body */}
        <div className="p-6 sm:p-8">
          {/* Header Section */}
          <div className="flex flex-col sm:flex-row gap-6 mb-6">
            {/* Child Photo */}
            <div className="flex-shrink-0">
              <div className="w-40 h-40 sm:w-48 sm:h-48 mx-auto sm:mx-0 rounded-2xl overflow-hidden bg-white p-2">
                <div className="w-full h-full rounded-xl overflow-hidden">
                  <img
                    src={data.childPhoto}
                    alt="Child"
                    className="w-full h-full object-cover"
                  />
                </div>
              </div>
            </div>

            {/* Content */}
            <div className="flex-1 text-white">
              <h2 className="text-3xl sm:text-4xl font-bold mb-2">
                {data.name}
              </h2>
              <p className="text-pink-400 text-lg mb-4">{data.subtitle}</p>
              
              <p className="text-sm sm:text-base leading-relaxed mb-4">
                {data.date}
              </p>

              <div className="text-sm sm:text-base leading-relaxed space-y-3">
                {data.description.map((paragraph, index) => (
                  <p key={index}>{paragraph}</p>
                ))}
              </div>
            </div>
          </div>

          {/* Action Buttons */}
          <div className="flex flex-col sm:flex-row gap-3 mb-6">
            <button className="flex-1 bg-white hover:bg-gray-100 text-gray-800 font-semibold py-3 px-4 sm:px-6 rounded-full transition-all duration-200 hover:scale-105 shadow-lg text-sm sm:text-base">
              পারিবারিক সাক্ষাৎকার নিন
            </button>
            <button className="flex-1 bg-gradient-to-r from-pink-500 to-pink-600 hover:from-pink-600 hover:to-pink-700 text-white font-semibold py-3 px-4 sm:px-6 rounded-full transition-all duration-200 hover:scale-105 shadow-lg text-sm sm:text-base">
              শেয়ার করুন
            </button>
            <button className="flex-1 bg-gradient-to-r from-red-600 to-red-700 hover:from-red-700 hover:to-red-800 text-white font-semibold py-3 px-4 sm:px-6 rounded-full transition-all duration-200 hover:scale-105 shadow-lg flex items-center justify-center gap-2 text-sm sm:text-base">
              <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 24 24">
                <path d="M23.498 6.186a3.016 3.016 0 0 0-2.122-2.136C19.505 3.545 12 3.545 12 3.545s-7.505 0-9.377.505A3.017 3.017 0 0 0 .502 6.186C0 8.07 0 12 0 12s0 3.93.502 5.814a3.016 3.016 0 0 0 2.122 2.136c1.871.505 9.376.505 9.376.505s7.505 0 9.377-.505a3.015 3.015 0 0 0 2.122-2.136C24 15.93 24 12 24 12s0-3.93-.502-5.814zM9.545 15.568V8.432L15.818 12l-6.273 3.568z"/>
              </svg>
              ইউটিউব দেখুন
            </button>
          </div>

          {/* Photo Gallery Grid */}
          <div className="grid grid-cols-2 gap-3 mb-6">
            {data.galleryPhotos.map((photo, index) => (
              <div key={index} className="rounded-lg overflow-hidden aspect-video bg-gray-700">
                <img
                  src={photo}
                  alt={`Gallery ${index + 1}`}
                  className="w-full h-full object-cover hover:scale-105 transition-transform duration-300"
                />
              </div>
            ))}
          </div>

          {/* YouTube Video Embed */}
          <div className="rounded-lg overflow-hidden aspect-video bg-black">
            <iframe
              width="100%"
              height="100%"
              src={`https://www.youtube.com/embed/${data.videoId}`}
              title="YouTube video player"
              frameBorder="0"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
              allowFullScreen
              className="w-full h-full"
            ></iframe>
          </div>
        </div>
      </div>
    </div>
  );
}